import { divIcon } from 'leaflet';
import { Marker, Popup } from 'react-leaflet'

function MarkerComponent(props: any) {
  // const markerIcon = icon({
  //   iconUrl: "/static/icons/marker.png",
  //   iconSize: [32, 32],
  //   iconAnchor: [16, 16],
  //   popupAnchor: [0, -16]
  // })

  const markerIcon = divIcon({
    iconSize: [props.iconSize, props.iconSize],
    iconAnchor: [props.iconSize / 2, props.iconSize / 2],
    html: `
      <div class="markerPopup-${props.className} size-${props.iconSize}">
        <span class="${props.color}">${props.title}</span>
        <img src="/bdm-desert/static/icons/${props.icon ? props.icon : `marker_${props.className}`}.png" alt="Marker">
      </div>`
    // html: `<span>${props.title}</span>`
  })

  return (
    <Marker position={props.position} icon={markerIcon} title={props.title} riseOnHover={true}>
      {/* <Popup>{props.title}</Popup> */}
    </Marker>
  )
}

export default MarkerComponent;
